//理赔单价计算 最新销售价 - 磨损费用 - 内垫费
export function claimPrice({ row, column }) {
  if (isNaN(row[column.property] || row[column.property] < 0)) {
    return;
  }
  var result = row.new_goods_price - row.abrasion_price - row.intube_price;
  if (result < 0 || isNaN(result)) {
    row.claim_price = 0
    return;
  }
  row.claim_price = Number(result).toFixed(2);
}
// 计算深度
export function computeDeep(scope) {
  var { row, column } = scope;
  if (isNaN(row[column.property] || row[column.property] < 0)) {
    row[column.property] = 0;
    return;
  }
  // 收费花深 = 出厂花深 - 剩余花深 - 厂免花深 - 公司照顾花深
  var result =
    row.raw_flowerdeep -
    row.surplus_flowerdeep -
    row.factory_flowerdeep -
    row.care_flowerdeep;
  if (result < 0 || isNaN(result)) {
    // this.$message.warning("收费花深不可以为负数");
    row.charge_flowerdeep = 0
    return;
  }
  row.charge_flowerdeep = Number(result).toFixed(2);
  // 磨损费用 = 毫米磨损费 * 收费花深
  var resultPrice = row.mm_wearprice * row.charge_flowerdeep;
  row.abrasion_price = !isNaN(resultPrice) ? Number(resultPrice).toFixed(2) : 0;
  claimPrice(scope);
}
