                <el-select clearable v-model="params.allocation_id" :disabled="!params.company_appraisal_goods_id > 0" filterable placeholder="请选择" @focus="InventoryInvok">
                  <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
                </el-select>
