            <el-col :span='12'>
              <el-form-item label="厂免花深">
                <el-input type="text" v-model="params.factory_flowerdeep" @input="computeDeep({row: params,column: { property: 'factory_flowerdeep' }})" @change="changeComputeDeep('factory_flowerdeep')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="公司照顾花深">
                <el-input type="text" v-model="params.care_flowerdeep" @input="computeDeep({row: params,column: { property: 'care_flowerdeep' }})" @change="changeComputeDeep('care_flowerdeep')" />
