              <el-form-item label="毫米磨损费" prop="mm_wearprice" :rules="{required: true,
    message: '请填写毫米磨损费', trigger: 'change' }">
                <el-input type="text" v-model="params.mm_wearprice" @input="computeDeep({row: params,column: { property: 'mm_wearprice' }})" @change="changeComputeDeep('mm_wearprice')" />
