<script>
import { getData } from "common/utils/public_function/commonUtils";
import { computeDeep, claimPrice, rowModule } from "./config/companyclaim_add";
export default {
  name: "aftersaleCompanyclaimModification",
  data() {
    return {
      authority: {},
      computeDeep: computeDeep,
      claimPrice: claimPrice,
      diseaseList: [], // 病象
      params: {
        symptom_id: []
      }
    };
  },
  methods: {
    // 失去焦点计算理赔单价
    changeClaimPrice(str) {
      this.$publicFution.toFixedTwo(this.params, str);
      this.claimPrice({ row: this.params, column: str });
    },
    // 失去焦点计算磨损费
    changeComputeDeep(str) {
      this.$publicFution.toFixedTwo(this.params, str);
      this.computeDeep({ row: this.params, column: str });
    },
    warehouseChange() {
      this.$set(this.params, "allocation_id", null);
      this.$set(this.params, "allocation_name", null);
    },
    goBack() {
      this.$router.push({
        path: "/aftersale/companyclaim"
      });
    },
    submit() {
      this.$refs.elForm.validate(result => {
        if (result) {
          if (this.params.claim_price < 0) {
            this.$message({
              message: "理赔单价为必填项且不可小于0",
              type: "warning"
            });
            return false;
          }
          var params = {
            customer_id: this.params.customer_id,
            company_claim_goods_id: this.params.company_claim_goods_id,
            business_time: this.params.business_time,
            company_id: this.params.company_id,
            inspector_id: this.params.inspector_id
          };
          Object.assign(
            params,
            this.$publicFution.filter(rowModule, this.params)
          );
          params.symptom_id = Array.isArray(this.params.symptom_id)
            ? this.params.symptom_id.join(",")
            : this.params.symptom_id;
          this.$axMethod.editClaim(params).then(response => {
            if (response.data.code == 200) {
              this.$message.success(
                response.data.msg ? response.data.msg : response.data.data
              );
              this.goBack();
            }
          });
        }
      });
    }
  },
  created() {
    this.EmployeeListInvok();
    this.getNEwlist();
    if (this.$route.query && this.$route.query.id) {
      this.$axMethod
        .getClaimInfo({
          company_claim_goods_id: this.$route.query.id
        })
        .then(response => {
          this.params = response.data.data;
          this.warehouseList.push({
            warehouse_id: this.params.warehouse_id,
            warehouse_name: this.params.warehouse_name
          });
          var idBox = this.params.symptom_id_str.split(","),
            symptomIdBox = [];
          idBox.forEach(val => {
            if (val !== "") {
              symptomIdBox.push(val - 0);
            }
          });
          this.$set(this.params, "symptom_id", symptomIdBox);
          this.allocationInventoryList.push({
            allocation_id: this.params.allocation_id,
            allocation_name: this.params.allocation_name
          });
        }); //商品下拉
    } else {
      this.$message.warning("单据有误");
    }
  }
};
</script>
